import React from 'react';

const SongTwo = () => {
    return (
        <React.Fragment>
            <p>The Italian anti-fascist song "Bella ciao" plays multiple times throughout the series and accompanies two emblematic key scenes: At the end of the first part the Professor and Berlin sing it in preparation for the heist, embracing themselves as resistance against the establishment, and in the second part it plays during the thieves' escape from the Mint, as a metaphor for freedom. Regarding the use of the song, Tokyo recounts in one of her narrations, "The life of the Professor revolved around a single idea: Resistance. His grandfather, who had fought against the fascists in Italy, taught him the song, and he taught us." The song was brought to the show by writer Javier Gómez Santander. He had listened to "Bella ciao" at home to cheer him up, as he had grown frustrated for not finding a suitable song for the middle of part 1. He was aware of the song's meaning and history and felt it represented positive values. "Bella ciao" became a summer hit in Europe in 2018, mostly due to the popularity of the series and not the song's grave themes.</p>
        </React.Fragment>
    );
}
 
export default SongTwo;