import React from 'react';

const Season01 = () => {
    return (
        <React.Fragment>
            <p>Part 1 begins with the aftermath of a failed bank robbery by a woman named "Tokyo," as a man called the "Professor" saves her from being caught by the police and proposes her a heist of drastic proportions. After a brief outline of the planned heist, the story jumps to the beginning of a multi-day assault on the Royal Mint of Spain in Madrid. The eight robbers are code-named after cities: Tokyo, Moscow, Berlin, Nairobi, Rio, Denver, Helsinki, and Oslo. Dressed in red jumpsuits with a mask of the Spanish painter Salvador Dalí, the group of robbers take 67 hostages as part of their plan to print and escape with €2.4 billion through a self-built escape tunnel. The Professor heads the heist from an external location. Flashbacks throughout the series show the five months of preparation in an abandoned hunting estate in the Toledo countryside; the robbers are not to share personal information nor engage in personal relationships, and the assault shall be without bloodshed.</p>
        </React.Fragment>
    );
}
 
export default Season01;